.is-hidden {
  visibility: hidden;
  opacity: 0;
}
.none {
  transform: translate(-50%, -50%);
}
.display-none-for-unsigned-user {
  display: none;
}
.button--orange {
  background-color: $button-background-color;
  color: $main-font-bright-color;
  border: inherit;
  transform: scale(1);
  will-change: filter, transform;
  transition: filter, transform 250ms cubic-bezier(0.165, 0.84, 0.44, 1);
  &:hover,
  &:focus {
    color: $main-font-bright-color;
    -webkit-box-shadow: 0px 0px 39px 3px rgba(255, 107, 1, 0.1);
    -moz-box-shadow: 0px 0px 39px 3px rgba(255, 107, 1, 0.1);
    box-shadow: 0px 0px 39px 3px rgba(255, 107, 1, 0.1);
    transform: scale(1.05);
  }
}
